body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Main";
  src: local("Main"),
  url(/static/media/Regular.5b89a8df.ttf) format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Main";
  src: local("Main"),
  url(/static/media/Bold.ca49f765.ttf) format("truetype");
  font-weight: bold;
}

/* Customize your main colors in :root variables */
:root {
  --light-background-color: #f7ff05;
  --main-background-color: #22240e;
  --dark-background-color: #1d1e0d;
  --title-text-color:#f7ff05;
}

body{
  background-color: #22240e;
  background-color: var(--main-background-color);
  font-family: 'Patrick Hand', cursive;
  font-size: 1.1em;
}

h2, p{
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}




.hovered-div {
    transition: all 0.3s;
    padding-bottom: 8px;
    padding-top: 8px;
    cursor: pointer;
}


.hovered-div:hover {
    background-color: #ffffff78;

}

.countDown-container {
    display: flex;
    width: 100%;
    margin-top: 1rem;
    margin-bottom: 0rem;
    flex-direction: row;
    align-content: center;
    align-items: center;
    justify-content: space-between;
    color: rgb(23, 253, 3);
    font-size: 18px;
}

.item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 20%;
}


